import VueI18n from './vue-i18n.js';//vue-i18n
import Vue from 'vue';
import storage from "storejs"

Vue.use(VueI18n);
import En from './locales/en.js';   // 
import Cn from './locales/cn.js';	  // 


const messages = {
	'En': { message: En },
	'Cn': { message: Cn },
}
 
var lang = storage.has("language") ? storage.get("language") : 'Cn';
window.localStorage.setItem('language',lang)

var i18n = new VueI18n({
  locale: lang,    // 
  messages
});

Vue.prototype.i18n = i18n; 
 
export default i18n