const tools = {};
import router from '../router'
import ElementUI from 'element-ui';

// 
import Vue from 'vue'
import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)

// 
tools.jump = (path,query) => {
  router.push({
    path,
    query
  })
}

// 
tools.copy = (value) => {
  if(!value||value==undefined) return ElementUI.Message.error("")
  Vue.prototype.$copyText(value).then(
    res=>{
      console.log(res)
      ElementUI.Message.success("")
    },
    err=>{
      console.log(err)
      ElementUI.Message.error("")
    }
  )
}

// 
tools.back = () => {
  router.go(-1)
}

// 
tools.success = (value) => {
  ElementUI.Message.success(value)
}

// 
tools.warning = (value) => {
  ElementUI.Message.warning(value)
}

// 
tools.info = (value) => {
  ElementUI.Message.info(value)
}

// 
tools.error = (value) => {
  ElementUI.Message.error(value)
}

export default tools